//VARIABLES
$base_color_blue: #1eacc7;
$base_color_white: #ffffff;




* {
	font-family: Roboto, sans-serif; }
body {
	margin-top: 49px; }
.header {
	position: relative; }
nav {
	height: 50px;
	background-color: #212121;
	position: fixed;
	width: 100%;
	z-index: 10000;
	transform: translateY(-100%); }
.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style-type: none;
	text-decoration: none;
	.menu_link {
		color: $base_color_white;
		font-size: 15px;
		font-weight: 300; }
	.menu_item {
		border-right: 2px dotted black;
		padding-right: 28px; } }
.subheader {
	position: absolute;
	width: 100%;
	height: 84px;
	background-color: rgba(0,0,0, .6);
	.subheader_logo {
		max-width: 100%; }
	.subheader_official {
		margin-top: 6px;
		opacity: 0.5;
		color: $base_color_white;
		font-size: 13px;
		font-weight: 400; }
	.subheader_phone {
		display: block;
		margin-top: 8px;
		color: $base_color_white;
		font-size: 22px;
		font-weight: 400;
		text-transform: uppercase;
		text-align: right;
		line-height: 12px;
		text-decoration: none; }
	.subheader_btn {
		margin-top: 5px;
		width: 172px;
		height: 46px;
		box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(to top, $base_color_blue 0%, #4ce2ff 100%);
		color: $base_color_white;
		font-size: 14px;
		font-weight: 400;
		text-transform: uppercase; } }
.col-md-4 {
	margin-top: 16px; }
.subheader_call {
	color: $base_color_white;
	text-align: right; }
.col-md-2 {
	margin-top: 16px; }
.promo {
	min-height: 800px;
	background: url(../icon/bg_section1.png) center center/cover no-repeat;
	padding: 150px 0 135px 0;
	.promo_header {
		color: $base_color_white;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 60px;
		line-height: 60px;
		margin: 0;
		text-align: center; }
	.promo_subheader {
		color: $base_color_white;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 36px;
		line-height: 36px;
		margin: 0;
		margin-top: 22px;
		text-align: center; }
	.promo_descr {
		color: $base_color_white;
		font-size: 18px;
		font-weight: 400;
		line-height: 24px;
		text-align: center;
		margin-top: 23px; }
	.promo_btn {
		display: block;
		width: 259px;
		margin: 118px auto 0 auto;
		height: 63px;
		box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(to top, $base_color_blue 0%, #4ce2ff 100%);
		font-size: 14px;
		font-weight: 400;
		text-transform: uppercase;
		color: $base_color_white; } }
.work {
	.row {
		display: flex;
		margin-top: 40px;
		align-items: center;
		justify-content: center;
		height: 205px; }
	.col-md-2 {
		display: flex;
		background-color: $base_color_blue;
		width: 113px;
		height: 113px;
		border-radius: 50%;
		align-items: center;
		justify-content: center; }
	.col-md-4 {
		display: block;
		margin-left: 15px;
		margin-top: 0; }
	.icon_head {
		font-size: 17px;
		line-height: 21px;
		color: $base_color_blue; }
	.icon_txt {
		font-size: 15px;
		line-height: 21px; }
	.tittle {
		color: #222222;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 38px;
		line-height: 48px;
		margin: 0;
		text-align: center; }
	.label {
		display: block;
		color: $base_color_white;
		font-size: 11px;
		margin: 70px auto 36px auto;
		padding-top: 5px;
		background-color: #222222;
		text-transform: uppercase;
		width: 113px;
		height: 25px;
		text-align: center; } }

.tittle {
	color: #222222;
	font-weight: 700;
	text-transform: uppercase;
	font-size: 38px;
	line-height: 48px;
	margin: 0;
	text-align: center; }

.subtittle {
	color: #222222;
	font-size: 17px;
	font-weight: 400;
	text-align: center; }


.label {
	display: block;
	color: $base_color_white;
	font-size: 11px;
	margin: 70px auto 36px auto;
	padding-top: 5px;
	background-color: #222222;
	text-transform: uppercase;
	width: 113px;
	height: 25px;
	text-align: center; }

.mobile {
	min-height: 864px;
	padding: 48px 0 79 0;
	background: url('../icon/bg_section3') center center/cover no-repeat;
	.row {
		margin-top: 32px; }

	&_item {
		position: relative;
		width: 320px;
		height: 280px;
		margin: 0 auto;
		margin-bottom: 19px;
		padding-top: 92px;

		&_1 {
			background: url('../icon/elem1_section3.png') center center/cover no-repeat; }
		&_2 {
			background: url('../icon/elem2_section3.png') center center/cover no-repeat; }
		&_3 {
			background: url('../icon/elem3_section3.png') center center/cover no-repeat; }
		&_4 {
			background: url('../icon/elem4_section3.png') center center/cover no-repeat; }
		&_5 {
			background: url('../icon/elem5_section3.png') center center/cover no-repeat; }
		&_6 {
			background: url('../icon/elem6_section3.png') center center/cover no-repeat; }
		&_subtittle {
			color: $base_color_white;
			font-size: 24px;
			font-weight: 400;
			line-height: 31px;
			text-transform: uppercase;
			text-align: center; }
		&_plus {
			display: flex;
			position: absolute;
			justify-content: center;
			align-items: center;
			width: 26px;
			height: 26px;
			background-color: #000000;
			margin: 0 auto;
			border-radius: 100%;
			bottom: 35px;
			left: 50%;
			transform: translateX(-50%);
			cursor: pointer; } } }
.car {

	.black {
		background-color: #000000;
		height: 490px;
		position: relative;
		display: flex;
		justify-content: center;
		align-content: center;
		&_header {
			position: relative;
			.text_1 {
				display: block;
				color: #000000;
				font-size: 11px;
				margin: 57px auto 33px auto;
				padding-top: 5px;
				background-color: $base_color_white;
				text-transform: uppercase;
				width: 113px;
				height: 25px;
				text-align: center; }
			.text_2 {
				color: $base_color_white;
				font-size: 38px;
				line-height: 48px;
				text-transform: uppercase;
				text-align: center;
				margin-bottom: 22px;
				font-weight: 700; }
			.text_3 {
				color: $base_color_white;
				font-size: 17px;
				text-transform: uppercase;
				text-align: center; }
			.uber_logo {
				left: 50%;
				transform: translateX(-50%);
				margin-top: 80px;
				cursor: pointer;
				z-index: 1000;
				position: absolute; } }
		.car_pct {
			position: absolute;
			top: 100%;
			transform: translateY(-50%); } }

	.white {
		background-color: $base_color_white;
		height: 400px;
		.white_txt_1 {
			text-align: center;
			color: #000000;
			margin-top: 350px;
			font-size: 17px;
			font-weight: 400; }
		.white_txt_2 {
			text-align: center;
			color: #000000;
			font-size: 17px;
			font-weight: 400; } } }

.properties {

	.tittle {
		margin: 33px auto; }

	.row {
		.col-md-6 {
			.properties_subtittles {
				font-size: 17px;
				line-height: 21px;
				color: $base_color_blue;
				margin-bottom: 20px; }
			.properties_subtittles_2 {
				color: red;
				margin-bottom: 20px;
				margin-top: 20px; }
			.col_text {
				color: $base_color_blue;
				margin-top: 20px; } } } }






















